<template>
	<div class="boxfarther">
		<div class="hide">
			<div class="imgfarther" ref="hidimg">
				<img :src="item" alt="img" v-for="(item, index) in imgarr" :key="index" class="img" :ref="`img${index}`" >
			</div>
		</div>
		<div class="buttombox">
			<img src="../assets/right.png" alt="" class="left" @click="left">
			<img src="../assets/left.png" alt="" class="right" @click="right">
		</div>
		<!-- <img src="../assets/img1.jpg" alt=""> -->
		
		<div class="tipfarther">
			<div class="defaulttip" v-for="(item, index) in imgarr" :key="`tip${index}`"></div>
			<div class="blacktip" ref="tipbalck"></div>
		</div>
	</div>
</template>

<script>
	// import $ from 'jquery';

	export default{
		name:'scollimg',
		props:{
			imgarr:[Array],
		},
		data() {
			return{
				imgindex:0
			}
		},
		mounted() {
			setInterval(()=>{
				
				if (this.imgindex==0){
					// tipbalck
					this.$refs.hidimg.style.transform='translateX(-50%)'
					this.$refs.tipbalck.style.transform='translateX(3.75rem)'
					this.imgindex=1
				}else{
					this.$refs.hidimg.style.transform='translateX(0%)'
					this.$refs.tipbalck.style.transform='translateX(0%)'
					this.imgindex=0
				}
			},3500)
			
			console.log(this.imgindex)
		},
		methods:{
			left:function(){
				console.log('向左')
				if(this.imgindex==1){
					this.$refs.hidimg.style.transform='translateX(0%)'
					this.$refs.tipbalck.style.transform='translateX(0%)'
					this.imgindex=1
				}
			},
			right:function(){
				console.log("向有")
				if(this.imgindex==0){
					this.$refs.hidimg.style.transform='translateX(-50%)'
					this.$refs.tipbalck.style.transform='translateX(3.75rem)'
					this.imgindex=1
				}
			}
		}
		
	}
</script>

