import { createRouter, createWebHistory } from 'vue-router'
import login from '../views/login.vue'
import userdefault from '../views/userdefault.vue'
import yanzhengmalogin from '../views/yanzhengmalogin.vue'
import register from '../views/register.vue'
import home from '../views/home.vue'
import navigatorTitle from '../views/navigatorTitle.vue'
import player from '../views/player.vue'
import uptoken from '../views/uptoken.vue'

const routerHistory = createWebHistory()
const routes= [
  {
    path: '/login',
    name: 'Login',
    component: login,
	meta:{
		title:'登录'
	},
	children:[
		{
			path: 'userdefault',
			name: 'userdefault',
			component: userdefault
		},
		{
			path: 'yanzhengmalogin',
			name: 'yanzhengmalogin',
			component: yanzhengmalogin
		},
		{
			path: 'register',
			name: 'register',
			component: register
		}
	]
  },
  {
	path: '/',
	name: 'home',
	component: home,
	meta:{
		title:'首页'
	},
	children:[
		{
			// http://localhost:8080/navigatorTitle
			path: '/navigatorTitle',
			name: 'navigatorTitle',
			component: navigatorTitle
		}
	]
  },
  {
	path: '/player',
	name: 'player',
	component: player,
	meta:{
		title:'播放'
	}
  },
  {
	path: '/uptokendsada',
	name: 'uptoken',
	component: uptoken,
	meta:{
		title:'上传'
	}
  },
]

const router = createRouter({
  history: routerHistory,
  routes
})

export default router
