<template>
	<div class='demo'>
		<video id="video" class="video-js vjs-default-skin vjs-big-play-centered" controls preload="none">
		</video>
	</div>
</template>

<script>
	import Video from 'video.js'
	import 'video.js/dist/video-js.css'
	export default{
		props:{
			firsturl:String
		},
		name:'videoplayer12',
		data(){
			return{
				url:this.firsturl,
			}
		},
		computed:{
			
		},
		mounted(){
			this.myPlayer=Video('video',{
						//确定播放器是否具有用户可以与之交互的控件。没有控件，启动视频播放的唯一方法是使用autoplay属性或通过PlayerAPI。
				controls:true,
				//自动播放属性,muted:静音播放
				muted:true,
				autoplay:true,
				//建议浏览器是否应在<video>加载元素后立即开始下载视频数据。
				preload:"auto",
				//设置视频播放器的显示宽度（以像素为单位）
				width:"720px",
				//设置视频播放器的显示高度（以像素为单位）
				height:"416px",
				//url
				//poster:'https://static.shuxuejia.com/img/video_image.png',//封面图片
				sources:[{
					src:this.url
				}],
				playbackRates:[0.5,1,1.5,2]//倍速播放
				
				})
			
		},
		watch:{
			firsturl:function(newvle){
				var that = this;
				this.url=newvle;
				console.log("视频源变化了")
				console.log(newvle)
				// Video('video',{}).dispose();
				console.log(this.myPlayer)
				var Player=Video('video');
				Player.src({
					src:that.url
				})
			}
		},
		methods:{
			
			
		},
	}
</script>

