<template>
	<div class="listfarther">
		<div class="titlename">{{title}}</div>
		<div v-for="item in classlist" class="classes" v-bind:key="item.targetName" @click="play(item.targetName)" >{{item.name}}</div>
	</div>
</template>

<script>
	import axios from 'axios'
	export default{
		data() {
			return {
				title:'',
				classlist:[]
			}
		},
		mounted() {
			var that = this;
			var list =[];
			axios.defaults.baseURL = '/video';//基础跟目录
			axios.get('video/videolist?lesson=%E4%B8%AD%E5%8C%BB%E7%96%97%E6%B3%95')//加在跟目录后的路径,在添加参数
			.then(function(data){
				if (data.data.length!=0){
					that.title=JSON.parse(data.data[0]).lesson;
					for (var i =0;i<data.data.length;i++){
						console.log(data.data[i])
						list.push(JSON.parse(data.data[i]))
					}
					that.classlist=list
					console.log(list)
				}else{
					// console.log(window.location)
					console.log(data)
					console.log("没有数据")
				}
				
			})
		},
		methods:{
			play:function(e){
				console.log(e)
				this.$emit("getplayNum",e);
			}
		}
	}
</script>

<style scoped>
	.listfarther{
		width: 15.625rem;
		height: 26rem;
		display: flex;
		flex-direction: column;
	}
	.titlename{
		width: 100%;
		height: 3.125rem;
		display: flex;
		justify-content: center;
		align-items: center;
		border-bottom: 0.0625rem solid black;
	}
	.classes{
		height: 1.875rem;
		width: 100%;
		display: flex;
		flex-direction: row;
		padding-left: 0.625rem;
		align-items: center;
	}
	.classes:hover{
		cursor: pointer;
	}
</style>
